.graph {
	overflow: hidden;
	position: relative;
	background-color: $blue;
	padding-top: 147px;
	min-width: 320px;
	@include md {
		overflow: visible;
		padding-top: 60px; }
	@include sm {
		padding-top: 30px; }

	&__header {
		position: relative;
		font-family: $montserrat;
		color: $white;
		max-width: 996px;
		margin: auto; }

	&__box {
		position: absolute;
		right: 104px;
		top: -104px;
		.icon {
			width: 96px;
			height: 112px; }
		@include sm {
			display: none; } }

	&__big-box {
		z-index: 1;
		position: absolute;
		left: -525px;
		top: 54px;
		.icon {
			width: 333px;
			height: 333px; }
		@include sm {
			display: none; } }

	&__title {
		font-weight: 600;
		font-size: 60px;
		line-height: 72px;
		@include md {
			font-size: 48px;
			line-height: 48px; }
		@include sm {
			text-align: center;
			min-width: 290px;
			margin: 0 auto;
			font-size: 30px;
			line-height: 30px; } }

	&__description {
		font-weight: 600;
		font-size: 24px;
		line-height: 48px;
		@include md {
			font-size: 18px;
			line-height: 36px; }
		@include sm {
			text-align: center;
			min-width: 290px;
			margin: 0 auto;
			font-size: 16px;
			line-height: 24px; } }

	&__buttons {
		margin-top: 32px;
		margin-right: 25px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin-right: 0; }
		@include sm {
			justify-content: space-around;
			flex-wrap: wrap;
			min-width: 290px;
			margin-left: -5px; } }

	&__item {
		@include sm {
			margin-left: 5px;
			margin-top: 10px; } }

	&__button {
		width: 137px;
		height: 83px;
		@include md {
			width: 100px;
			height: 62px; }
		@include sm {
			width: 90px;
			height: 50px; } }

	&__content {
		position: relative;
		width: 100%;
		max-width: 1844px;
		height: 564px;
		margin: 26px auto 0;
		background: $blue url("../img/forecast.png") no-repeat right;
		@include md {
			width: auto;
			margin: 42px 36px 0; }
		@include sm {
			height: 490px;
			margin: 120px 6px 0 15px; } }

	&__stat {
		font-family: $montserrat;
		color: $white;
		position: absolute;
		display: flex;
		align-items: baseline;
		top: 45px;
		left: 485px;
		@include md {
			top: 22px;
			left: 52px; }
		@include sm {
			top: -90px;
			left: 0px;
			width: 100%;
			justify-content: space-between; } }

	&__stat-currency {
		margin-right: 28px;
		text-transform: uppercase;
		font-size: 24px;
		font-weight: 700;
		display: flex;
		flex-direction: column;
		@include sm {
			font-size: 18px; }
		&:before,&:after {
			content: "-";
			color: transparent;
			font-size: 12px;
			@include sm {
				font-size: 10px; } } }

	&__stat-item {
		text-align: right;
		margin: 0 18px;
		@include sm {
			margin: 0 9px; } }

	&__stat-title {
		font-size: 12px;
		font-weight: 400;
		@include sm {
			font-size: 10px; } }

	&__stat-value {
		font-size: 24px;
		font-weight: 700;
		@include sm {
			font-size: 18px; } }

	&__stat-change {
		font-size: 12px;
		font-weight: 400;
		color: $green;
		@include sm {
			font-size: 10px; } } }




