.title {
	font-family: $montserrat;
	font-size: 72px;
	font-weight: 600;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; }

	&_blue {
		color: $blue; }

	&_white {
		color: $white; }

	&_md {
		font-size: 36px;
		@include md {
			font-size: 36px; }
		@include sm {
			font-size: 24px; } } }
